
import Vue from 'vue'
import Router from 'vue-router'

import UserLogin from './../components/UserLogin.vue' //
import UserRegister from './../components/UserRegister.vue' //
import ForgetPassword from './../components/ForgetPassword.vue' //
import UserCenter from './../components/UserCenter.vue' //
import Home from './../components/Home.vue' //
import GroupList from './../components/GroupList.vue' //
import GroupListTw from './../components/GroupListTw.vue' //
import ShoppingList from './../components/ShoppingList.vue' //
import UserEmbody from './../components/UserEmbody.vue' //
import UserEmbodyList from './../components/UserEmbodyList.vue' //
import UserRecharge from './../components/UserRecharge.vue' //
import AboutIntroduce from './../components/AboutIntroduce.vue' //
import CustomService from './../components/CustomService.vue' //
import PersonalInformation from './../components/PersonalInformation.vue' //
import UpdatePwd from './../components/UpdatePwd.vue' //
import chongzhi from './../components/chongzhi.vue' //
import zhongjiang from './../components/zhongjiang.vue' //
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '8-',
      }

    },
    {
      path: "/UpdatePwd",
      name: "UpdatePwd",
      component: UpdatePwd,
      meta: {
        title: "8-"
      }
    },
    {
      path: '/UserRegister',
      name: 'UserRegister',
      component: UserRegister,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/UserLogin',
      name: 'UserLogin',
      component: UserLogin,
      meta: {
        title: '8-'
      }
    },
    {
      path: '/ForgetPassword',
      name: 'ForgetPassword',
      component: ForgetPassword,
      meta: {
        title: '8-',

      }

    },
    {
      path: '/UserCenter',
      name: 'UserCenter',
      component: UserCenter,
      meta: {
        title: '8-',
        requireAuth: true,
      }

    },
    {
      path: '/GroupList',
      name: 'GroupList',
      component: GroupList,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/GroupListTw',
      name: 'GroupListTw',
      component: GroupListTw,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/ShoppingList',
      name: 'ShoppingList',
      component: ShoppingList,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/UserEmbody',
      name: 'UserEmbody',
      component: UserEmbody,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/UserEmbodyList',
      name: 'UserEmbodyList',
      component: UserEmbodyList,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/PersonalInformation',
      name: 'PersonalInformation',
      component: PersonalInformation,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/UserRecharge',
      name: 'UserRecharge',
      component: UserRecharge,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/AboutIntroduce',
      name: 'AboutIntroduce',
      component: AboutIntroduce,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/CustomService',
      name: 'CustomService',
      component: CustomService,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/chongzhi',
      name: 'chongzhi',
      component: chongzhi,
      meta: {
        title: '8-',
      }

    },
    {
      path: '/zhongjiang',
      name: 'zhongjiang',
      component: zhongjiang,
      meta: {
        title: '8-',
        requireAuth: true,
      }

    },
  ],
  mode: "history"//#
})




