<template>
  <div id="user-login">
    <mt-header title="" id="registerHerder">
      <router-link to="/UserLogin" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="registerFrom">

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPhone" v-model="phone" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'') " ng-pattern="/[^a-zA-Z]/" maxlength=11></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPassword" v-model="password" type="password" placeholder=""></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPwd" v-model="urepwd" type="password" placeholder=""></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerUserCode" v-model="payPassword" required="required" placeholder="" ></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-button id="registerLogin" @click="UserRegister"  style="width:100%" type="primary"></el-button>
        </el-col>
      </el-row>
      <router-link to="UserLogin" class="ToLogin"><span></span></router-link>

    </div>

  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  import axios from 'axios';
  export default {
    name: 'user-login',
    data () {
      return{
        phone:'', //
        password:'',//
        urepwd:'',//
        payPassword:'', //
        parentId: ''
        //code:'',
        //show: true,
       ///count: '',
        //timer: null
      }
    },
    methods : {
      UserRegister:function(){
        //

        //UserCode = this.UserCode,
        let reg= /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;;
        if(this.phone==''){
          alert("");
          return false;
        }else if(!reg.test(this.phone)){
          alert("");
          return false;
        }

        if (this.password == ''){
          alert('');
          return false;
        }
        if (this.password != this.urepwd) {
          alert('');
          return false;
        }
        if (this.payPassword == ''){
          alert('');
          return false;
        }

        axios.post("/api/user/save.dnl",{
          phone: this.phone,
          password: this.password,
          payPassword: this.payPassword,
          parentId: this.parentId,
        }).then((data)=>{
          //alert(1)
          console.log(data)
          if(data.data.code == 10000){
            alert(data.data.msg)
            //location.href = 'UserLogin'
            this.$router.push('/UserLogin');
            return true
          }else{
            //alert(111)
            alert(data.data.msg)
            //location.href = 'UserLogin'
            return false
          }
        })
      },
      // 
      // getCode(){
      //   const time_count= 10;
      //   const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
      //   if(this.phone==''){
      //     alert("");
      //     return false;
      //   }else if(!reg.test(this.phone)){
      //     alert("");
      //     return false;
      //   }
      //   if (!this.timer) {
      //     this.count = time_count;
      //     this.show = false;
      //     this.timer = setInterval(() => {
      //       if (this.count > 0 && this.count <= time_count) {
      //         this.count--;
      //       } else {
      //         this.show = true;
      //         clearInterval(this.timer);
      //         this.timer = null;
      //       }
      //     }, 1000)
      //   }
      // },

    },
    mounted () {
      let sharedid = this.$route.query.parentId
      if (sharedid === undefined) {
        this.parentId = ''
      }
      this.parentId = sharedid
    }
  }
</script>

<!--  "scoped"  -->
<style>

  /*  */
  #registerHerder{background-color: #FF414F;color: #fff;}

  /* from */
  .registerFrom{background: #fff;padding: 0.5rem 0;}
  .el-input{display: block}
  .register-el-col{float: none !important;margin: 0 auto 0.3rem auto; position: relative}

  /* input */
  #registerPhone,#registerPassword,#registerPwd{background: #F7F4F8;border: none;color: #b7b5b5;}
  #registerUserCode{background: #F7F4F8;border: none;color: #b7b5b5;}

  /*  */
  .registerCount{position: absolute;top: 0;height: 100%;right: 0;width: 42%;background: rgba(255,65,79,1);padding: 0 8px;color: #fff;}

   /* button */
  #registerLogin{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}

  /*  */
  .ToLogin{color: rgba(255,129,126,1);font-size: 0.28rem;text-align: center;display: inline-block;width: 100%;font-weight: 600;}
</style>



